<template>
  <div class="order">
    <!--<div class="top">
      <header>
         <i class="iconfont icon-wenben-shixin"></i>
        <span style="display: inline-block;padding-left: 0.05rem;">订单管理</span>
      </header>
      <p></p>
    </div>-->
    <div>
      <div class="search">
        <div class="item item_one">
          <span style="margin-bottom: 0.1rem;display: inline-block">用户名称</span>
          <Input placeholder="请输入用户名称" v-model.trim="name"></Input>
        </div>
        <div class="item">
          <span style="margin-bottom: 0.1rem;display: inline-block">订单状态</span>
          <Select v-model="state" style="width:200px">
            <Option v-for="item in orderList" :value="item.value" :key="item.value">{{ item.label }}</Option>
          </Select>
        </div>
        <div class="item">
          <span style="margin-bottom: 0.1rem;display: inline-block">订单号</span>
          <Input placeholder="请输入订单号" v-model.trim="order"></Input>
        </div>
        <div class="item">
          <span style="margin-bottom: 0.1rem;display: inline-block">手机号</span>
          <Input placeholder="请输入手机号" v-model.trim="tel"></Input>
        </div>
        <div class="item">
          <span style="margin-bottom: 0.1rem;display: inline-block">日期</span>
          <DatePicker type="daterange" :options="options2" placement="bottom-end" placeholder="选择起止日期" style="width: 200px" v-model="date"></DatePicker>
        </div>
        <div class="item item_last">
          <a href="javascript:void(0)" @click="search" class="search_button">搜索</a>
        </div>
      </div>
      <div>
        <div style="width: calc(100vw - 3.5rem);margin-top: 0.1rem">
          <!--<Table  :columns="columns" :data="data" :no-data-text="msg" ></Table>-->
          <table>
            <tr>
              <th>订单状态</th>
              <th>买家名称</th>
              <th>订单号</th>
              <th>支付时间</th>
              <th>总计金额</th>
              <th>操作</th>
            </tr>
            <tr class="td" v-for="(item,key) in data">
              <td>{{item.state}}</td>
              <td>{{item.username}}</td>
              <td>{{item.order_sn}}</td>
              <td>{{item.prepay_time}}</td>
              <td>{{item.total_price}}</td>
              <td>
                <a href="javascript:void(0)" class="info" style="display: inline-block;margin-right: 0.08rem" @click="show(item)">查看</a>
              </td>
            </tr>
          </table>
          <p v-if="data.length===0" class="td_data">
            <img src="../../../assets/no-data.png" class="img_box" />
          </p>
        </div>
        <footer style="padding: 0.2rem 0;box-shadow: 5px 5px 10px #E7E7E7;background: white;width: 96%;padding-right: 0.2rem;">
          <Page :total="total" size="small" show-total show-elevator show-sizer @on-change="changePage" @on-page-size-change="changeSize"></Page>
        </footer>
      </div>
    </div>
  </div>
</template>
<script>
  export default {
    data() {
      return {
        total: 0,
        orderList: [{
            value: 0,
            label: '未支付'
          },
          {
            value: 1,
            label: '已支付'
          },
          {
            value: 2,
            label: '已退款'
          },
          {
            value: 3,
            label: '已入住'
          },
          {
            value: 4,
            label: '退款中'
          },
          {
            value: 5,
            label: '已完成'
          },
        ],
        state: 1,
        name: "",
        order: "",
        tel: "",
        total: 0,
        date: '',
        startTime: '',
        endTime: "",
        options2: {
          shortcuts: [{
              text: '最近一周',
              value() {
                const end = new Date();
                const start = new Date();
                start.setTime(start.getTime() - 3600 * 1000 * 24 * 7);
                return [start, end];
              }
            },
            {
              text: '最近一个月',
              value() {
                const end = new Date();
                const start = new Date();
                start.setTime(start.getTime() - 3600 * 1000 * 24 * 30);
                return [start, end];
              }
            },
            {
              text: '最近三个月',
              value() {
                const end = new Date();
                const start = new Date();
                start.setTime(start.getTime() - 3600 * 1000 * 24 * 90);
                return [start, end];
              }
            }
          ]
        },
        msg: "数据加载中.....",
        data: []
      }
    },
    methods: {
      show(item) {

        let url = `/${this.$route.params.appid}/hotel/hotelIn/` + item.id;
        this.$router.push(url)
      },
      //        封装
      getOrderList(name, order, tel, startTime, endTime, state, page, num) {
        this.post("custom/Hotel/get_order_list", {
          page: page,
          limit_num: num,
          state: this.state,
          username: name,
          order_sn: order,
          user_tel: tel,
          start_time: startTime,
          end_time: endTime,
        }).then(res => {
          if(res.data.code === 10000) {
            this.data = res.data.data;
            this.total = res.data.number
            this.data.forEach(item => {
              item.prepay_time = new Date(item.prepay_time * 1000).toLocaleString();
              if(item.state === 1) {
                item.state = "已付款"
              } else if(item.state === 2) {
                item.state = "已退款"
              } else if(item.state === 3) {
                item.state = "已入住"
              } else if(item.state === 4) {
                item.state = "退款中"
              } else if(item.state === 0) {
                item.state = "未付款"
              } else if(item.state === 5) {
                item.state = "已完成"
              }
            });
            if(this.data.length === 0) {
              this.msg = "暂无数据";
            }
          } else {
            this.$Modal.error({
              title: "获取失败",
              content: res.data.msg_test
            });
          }
        });
      },
      //        搜索
      search() {
        if(this.date[0] && this.date[1]) {
          this.startTime = Math.ceil(new Date(this.date[0]).getTime() / 1000);
          this.endTime = Math.ceil(new Date(this.date[1]).getTime() / 1000);
        }
        this.getOrderList(this.name, this.order, this.tel, this.startTime, this.endTime, 1, 1, 10);
      },
      changePage(page) {
        this.getOrderList(this.name, this.order, this.tel, this.startTime, this.endTime, 1, page, 10);
      },
      changeSize(val) {
        this.getOrderList(this.name, this.order, this.tel, this.startTime, this.endTime, 1, 1, val);
      }
    },
    mounted() {
      this.$store.commit('setStatus', false);
      this.getOrderList();
    },
    activated() {
      this.$store.commit('setStatus', false);
    },
  }
</script>
<style>
  @import '../../../../static/base.css';
  @import url("../../../../static/table.css");
</style>


